.bdamage .main {
    padding-bottom: 0.5rem;
}

.bdamage .sborder {
    margin-top: 1rem;
}

/* 文字复选框简单效果 */
.bdamage input[type=checkbox] {
    /* 去除复选框的默认效果 */
    -webkit-appearance: none;
    -moz-appearance: none;
    display: none;
}

.bombdamage .myinput{
    position: relative;
    /* top: -0.5rem; */
}
.bombdamage>div{
    height: 2rem;
    line-height: 1.7rem;
    vertical-align: top;
    margin-top: 0.64rem;
}
.bombdamage>div span{
    vertical-align:top;
    line-height: 2rem;
}

.bdamage label {
    display: inline-block;
    border: 0.1rem solid var(--theme);
    border-radius: 0.4rem;
    position: relative;
    padding: 0.555rem 1rem;
    text-align: center;
    cursor: pointer;
    margin-top: 1rem;
    margin-left: 1rem;
    margin-right: 0.2rem;
    transition: 0.1s;
}
.bdamage .i4{
    width: 14rem;
}
.bdamage label:hover {
    box-shadow: 0 0 0.2rem var(--wBorder2);
}

.bdamage input[type=checkbox]:checked+label::before {
    /* 绿色背景 */
    content: '';
    /* 宽高均为0时, 只保留border部分 */
    width: 0px;
    height: 0px;
    position: absolute;
    /* 设置为-1, 防止缩放时产生缝隙 */
    bottom: -0.1rem;
    right: -0.1rem;
    /* 详见t1 */
    border-bottom: 0.889rem solid var(--theme);
    border-left: 1.6667rem solid transparent;
}

.bdamage input[type=checkbox]:checked+label::after {
    /* ✔ 效果 */
    content: '';
    position: absolute;
    width: 0.27778rem;
    height: 0.55556rem;
    right: 0.27778rem;
    bottom: 0.04111rem;
    border: 0.056rem solid white;
    border-top: 0;
    border-left: 0;
    background: transparent;
    transform: rotate(35deg);
}

.bdamage .findamage {
    font-size: 1.4rem;
    font-weight: bold;
    line-height: 4rem;
    /* background-color: #fff; */
    height: 4rem;
}

.bdamage .main>div:nth-child(1) {
    height: 80%;
    overflow: auto;
}

.bdamage .main>div:nth-child(2) {
    height: 20%;
    overflow: hidden;
    padding: 0 0.4rem;
    padding-bottom: 2.4rem;
}

