* {
    box-sizing: border-box;
    color: var(--color);
}

*::selection {
    background-color: var(--theme);
    color: #fff
}

::-webkit-scrollbar {
    display: none;
}

h1 {
    width: 100%;
    font-size: 2rem;
    font-weight: bolder;
    text-align: center;
    letter-spacing: 0.4rem;
}

h2 {
    width: 100%;
    font-size: 1.7rem;
    font-weight: bolder;
    text-align: center;
    letter-spacing: 0.3rem;
}

h3 {
    width: 100%;
    font-size: 1.4rem;
    text-align: center;
    letter-spacing: 0.24rem;
}

h4 {
    width: 100%;
    font-size: 1.16rem;
    text-align: center;
    letter-spacing: 0.2rem;
    border-bottom: 0.1rem solid var(--theme);
    margin-top: 1.2rem;
    margin-bottom: 0.5rem;
}

