.watch {
    display: inline-block;
    width: 8rem;
    height: 2rem;
    line-height: 1.7rem;
    margin-left: 1rem;
    margin-right: 0.2rem;
    font-size: 1.1rem;
    text-align: center;
    text-align-last: center;
    border-radius: 0.4rem;
    border: 0.1rem solid var(--color3);
    outline: none;
    padding: 0 1rem;
    color: var(--theme);
    background-color: #fff;
}

.select-2 {
    width: 12rem;
}

.select-3 {
    width: 16rem;
}

.btn {
    display: inline-block;
    width: 8rem;
    height: 3rem;
    line-height: 3rem;
    text-align: center;
    border-radius: 0.4rem;
    border: 0.1rem solid var(--color3);
    background-color: var(--cColor2);
    cursor: pointer;
    margin: 0 1rem;
    user-select: none;
}

label {
    user-select: none;
}

.btn:hover {
    box-shadow: 0 0 0.2rem var(--color3);
}

input {
    outline: none;
    border-radius: 0.4rem;
    font-size: 1.2rem;
    text-align: center;
    margin-left: 1rem;
    margin-right: 0.2rem;
    background-color: var(--color4);
    height: 2rem;
    border: 0.1rem solid var(--color5);
    /* box-shadow: 0 0 0.3rem var(--color3); */
}

input[type='range'] {
    outline: none;
    border-radius: 0.4rem;
    font-size: 1.2rem;
    text-align: center;
    position: relative;
    top: 0.57rem;
    margin-left: 1rem;
    margin-right: 0.2rem;
    -webkit-appearance: none;
    -moz-appearance: none;
    height: 2rem;
    overflow: hidden;
    background-color: var(--cColor);
    cursor: pointer;
    transition: 0.1s;
}

input[type='range']:hover {
    border: 0.1rem solid var(--color5);
}

input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    position: relative;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    background-color: var(--theme);
    border: 0.0001rem solid transparent;
    margin-top: 0px;
    border-image: linear-gradient(var(--theme), var(--color5)) 0 fill / 8 20 8 0 / 0 0 0 99vw;
    /*绘制元素外矩形*/
}

.danger {
    background-color: var(--danger);
    color: var(--color4);
}

.inp-1 {
    width: 5rem;
}

.inp-2 {
    width: 8rem;
}

.inp-3 {
    width: 10rem;
}

.inp-4 {
    width: 12rem;
}

.inp-5 {
    width: 18rem;
    font-size: 1rem;
}

.inp-6 {
    width: 30rem;
    font-size: 1rem;
}

.h {
    height: 2rem;
    line-height: 2rem;
    margin-top: 0.8rem;
    margin-bottom: -1rem;
}

.wrapper {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: row;
}

.border {
    padding: 1rem;
    margin: 1rem;
    border-radius: 2rem;
    border: .2rem solid var(--wBorder);
    transition: 0.1s;
    box-shadow: 0 0 0.3rem var(--boxshawo);
}

.sborder {
    padding: 1rem;
    margin: 0.4rem;
    border-radius: 1rem;
    border: .12rem solid var(--color5);
    transition: 0.1s;
}

.sborder:hover {
    box-shadow: 0 0 0.3rem var(--color5);
}

.border:hover {
    box-shadow: 0 0 0.6rem var(--wBorder2);
}

.topbar {
    height: 8vh;
    /* background-color: var(--cColor2); */
}

.left {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 22vw;
    flex-grow: 0;
    overflow: hidden;
}

.lefttop, .leftbottom {
    flex-grow: 1;
    height: 20%;
    background-color: var(--cColor2);
}

.main {
    right: 0;
    top: 0;
    background-color: var(--cColor);
    overflow: auto;
    flex-grow: 1;
}

.row {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 0.4rem;
}

.col-1 {
    grid-column: span 1;
}

.col-2 {
    grid-column: span 2;
}

.col-3 {
    grid-column: span 3;
}

.col-4 {
    grid-column: span 4;
}

.col-5 {
    grid-column: span 5;
}

.col-6 {
    grid-column: span 6;
}

.col-7 {
    grid-column: span 7;
}

.col-8 {
    grid-column: span 8;
}

.col-9 {
    grid-column: span 9;
}

.col-10 {
    grid-column: span 10;
}

.col-11 {
    grid-column: span 11;
}

.col-12 {
    grid-column: span 12;
}