.bsuit .main{
    overflow: hidden;
}

.singlesuit {
    padding: 1rem 0;
    /* padding-bottom: 0.2rem !important; */
}
.singlesuit.sborder{
    padding-bottom: 0.2rem;
}
.singlesuit .mainchoose{
    margin-top: 0.2rem;
}
.singlesuit .mainchoose .big{
    width: 12rem;
    margin-left: 0.2rem;    
}
.singlesuit .mainchoose .reset{
    top: -0.6rem;
}
.singlesuit .sborder{
    padding: 1rem 0.4rem;
    /* vertical-align: top; */
}
.suit h2{
    margin-bottom: 1rem;
}
.suit{
    margin-top: 1rem;
    position: relative;
    height: 100%;
    transition: 0.4s;
}
.suit li{
    position: relative;
}
.singlesuittop{
    margin-left: 0.4rem;
}
.suit .singlesuit{
    margin-bottom: 1rem ;
    /* background-color: #fff; */
}
.singlesuit .singlesuittop .myselect{
    width: 8rem;
    margin-left: 0;
    margin-right: 1rem;
}
.singlesuit li input{
    width: 5.5rem;
}
.singlesuit img{
    position: absolute;
    top: 1.4rem;
    right: 1.6rem;
    /* border: 1px solid black; */
    width: 3.6rem;
    /* background-color: #fff; */
    filter: brightness(50%);
}
.singlesuit .smallcitiao{
    margin-top: -0.4rem;
    /* margin-right: -3rem; */
}
.singlesuit:nth-child(4) .smallcitiao:nth-child(5) .myselect .list ul li:last-child,.singlesuit:nth-child(5) .myselect .list ul li:last-child .smallcitiao:nth-child(5),
.singlesuit:nth-child(6) .smallcitiao:nth-child(5) .myselect .list ul li:last-child{
    margin-bottom: 1.2rem;
}
.singlesuit:nth-child(4) .smallcitiao:nth-child(6) .myselect .list ul li:last-child,.singlesuit:nth-child(5) .myselect .list ul li:last-child .smallcitiao:nth-child(6),
.singlesuit:nth-child(6) .smallcitiao:nth-child(6) .myselect .list ul li:last-child{
    margin-bottom: 3.6rem;
}
.citiao {
    width: 9rem;
    margin-left: 0.2rem;
    margin-right: 0.2rem;
}
.num {
    width: 5.2rem;
    margin-left: 0.6rem;
    font-size: 0.96rem;
}
.singlesuit .addnum{
    width: 5.6rem ;
    /* display: inline-block; */
    /* background-color: #fff; */
    /* margin-left: 0.6rem; */
    margin-right: -0.2rem;
    font-size: 1rem;
    position: relative;
    top: -0.7rem;

}
.sss div{
    word-spacing: 0.7rem;
    flex: 0;
}
.footer{
    display: flex;
    flex: 0;
}
.artifact{
    width: 28%;
    word-spacing: 0.7rem;
}
.grow{
    flex: 1;
}
.suitfooter{
    display: flex;
    /* width: 100%; */
}
.suitcase{
    width: 14.6%;
    height: 18rem;
}
.suitcase select{
    position: relative;
    left: -0.56rem;
}
.assess{
    flex: 1;
}
.assess li{
    display: flex;
    align-items: baseline;
    /* justify-content: space-around; */
    /* background-color: #fff; */
    margin-bottom: -0.4rem;
}
.assess li span{
    /* line-height: 1.8rem; */
    /* height: 2rem; */
    text-align: center;
    /* flex: 1 0; */
    display: inline-block;
    width: 6.8rem;
}
.assess li span:nth-child(2){
    width: 5.6rem;
    /* background-color: #fff; */
}
.assess li>img{
    width: 2.8rem;
    margin-left: 1rem;
    margin-right: 0.8rem;
    position: relative;
    top: 0.7rem;
    filter: brightness(50%);
    
    /* background-color: #fff; */
}
.assess li .tips img{
    width: 0.7rem;
}
.assess li .tips>.txtwrapper{
    top: 1.6rem;
    padding: 0.5rem;
    font-size: 0.9rem;
    line-height: 1.4rem;
}
.assess li{
    margin-top: 0.6rem;
}
.assess>div:last-child{
    font-size: 1.3rem;
    line-height: 0.11rem;
    text-align: center;
    margin-top: 2.7rem;
}
.singlesuit .btn{
    display: inline-block;
    position: relative;
    width: 4rem;
    height: 2rem;
    top: -0.05rem;
    background-color: #fff;
    color: var(--theme);
    line-height: 1.8rem;
}
.assess .title{
    margin-top: 1rem;
}
.assess .title>div{
    /* width: 40%; */
    display: flex;
    /* justify-content: space-around; */
    height: 1rem;
    margin-bottom: -1rem;
}
.assess .title>div span{
    display: inline-block;
    width: 6.8rem;
    font-size: 0.96rem;
    color: var(--color5);
    text-align: center;
}
.assess .title>div span:nth-child(1){
    width: 2.8rem;
    margin-left: 1rem;
    margin-right: 0.8rem;
    position: relative;
}
.assess .title>div span:nth-child(2){
    width: 5.6rem;
}
/* .assess .title>div:nth-child(1){
    margin-left: 6.4rem;
}
.assess .title>div:nth-child(2){
    margin-right: 5.4rem;
} */
.hidden{
    display: none;
}
.right1{
    position: absolute;
    right: 10rem;
    bottom: 1.4rem;
    bottom: 1.4rem;
}
.left1{
    position: absolute;
    left: 10rem;
    bottom: 1.4rem;
}
.suit.run .right1, .suit.run .left1{
    visibility: hidden;
}
.right2{
    position: absolute;
    right: 10rem;
    bottom: 0.8rem;
}
.left2{
    position: absolute;
    left: 10rem;
    bottom: 0.8rem;
}

.suit.run{
    margin-top: -61.4%;
}
.bsuit .other{
    height: 100%;
    position: relative;
    transition: 0.4s;
    /* transform: translate(0,100%); */
}
.taozhuang .myselect .list ul li:last-child{
    margin-bottom: 5rem;
}
.grow .tips{
    margin-right: 1.4rem;
}
.bsuit .suitcase>div{
    margin-left: -0.4rem;
}
.bsuit .fight{
    margin-bottom: 0.3rem;
}
.bsuit .other .grow>div:last-child, .bsuit .other .grow>div:nth-child(6){
    margin-bottom: -0.4rem ;
    /* background-color: #fff; */
}