.nav{
    display: flex;
    justify-content: flex-end;
}
.bchoose .main{
    position: relative;
}
.choose{
    position: absolute;
    top: 5rem;
    left: 1rem;
    right: 1rem;
    bottom: 2rem;
    overflow: auto;
}
.search{
    height: 3rem;
    margin-right: auto;
}
.nav>div{
    width: 3rem;
    height: 3rem;
    background-color: var(--color3);
    border-radius: 50%;
    margin-left: 0.4rem;
    position: relative;
    cursor: pointer;
}
.nav .hr{
    visibility: hidden;
    margin-right: -1rem;
}
.nav img{
    width: 75%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.choose ul{
    margin-top: 2.4rem;
    width: 100%;
    overflow: auto;
}
.choose ul li{
    position: relative;
    cursor: pointer;
}
.choose ul li>img:nth-of-type(1){
    border-radius: 50%;
    width: 5rem;
    height: 5rem;
    border: 0.14rem solid var(--color3);
    margin-left: 1rem;
}
.choose ul li>img:nth-of-type(2){
    position: absolute;
    width: 1.6rem;
    left: 1.66rem;
    top: 1.2rem;

}
.choose ul li>img:nth-of-type(3){
    position: absolute;
    width: 1.6rem;
    left: 5.66rem;
    top: 1.2rem;
}


.choose ul li>div:nth-of-type(1){
    position: relative;
    bottom: -0.4rem;
    text-align: center;
}
.choose ul li>div:nth-of-type(2){
    position: absolute;
    width: 60%;
    top: 0.2rem;
    right: -1rem;
    margin-top: 1.4rem;
}
.choose ul li>div:nth-of-type(2) img{
    width: 1.44rem;
}
.choose ul li>img:nth-of-type(4){
    position: absolute;
    right: 1.2rem;
    bottom: 0.5rem;
    width: 1.44rem;
    /* border-radius: 50%; */
}
.choose ul li>img:nth-of-type(4):hover{
    transition: 0.4s;
    transform: rotate(180deg);
}

.choose .iconchoose{
    background-color: var(--theme);
}

.choose ul li>img:nth-of-type(1).err{
    border: 0.4rem double white ;
}
.choose p{
    position: relative;
    width: 0;
}
.choose .err+p::after{
    content: attr(txt);
    color: white;
    display: block;
    position: absolute;
    z-index: 9;
    font-size: 2rem;
    top: -3.7rem;
    left: 2.52rem;
    width: 2rem;
    height: 100%;
    height: 2rem;
    text-align: center;
}