.descript{
    overflow: auto;
}
.descript span{
    line-height: 1.8rem;
    margin-left: 1rem;
}
.descript ul{
    margin-top: 0.3rem;
    margin-bottom: 0.8rem;
    margin-left: 2rem;
    list-style-type: disc;
}
.descript li{
    line-height: 1.6rem;
}
.descript a{
    color: var(--color2);
    background-color: var(--color3);
    text-decoration: none;
}