.sdamage{
    font-size: 1rem;
}
.sdamage h3{
    margin-top: -0.3rem;
    margin-bottom: 0.5rem;
}
.sdamage h3:nth-child(1){
    margin-top: -0.3rem;
    margin-bottom: 0.5rem;
    display: flex;
    justify-content: space-between;
    font-size: 1.2rem;
}
.sdamage .base>div img{
    width: 1.4rem;
    height: 1.4rem;
    margin-left: 1.4rem;
}
.sdamage .base div div img{
    background-color: #707070;
    border-radius: 5px;
    position: relative;
    left: -0.6rem;
}
.sdamage .base div:nth-child(2) div img,.sdamage .base div:nth-child(3) div img{
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 5px;
}

.sdamage .base div div{
    position: relative;
    right: 1rem;
    margin-right: 1.4rem;
    width: 2rem;
    /* background-color: rgba(255, 255, 255, 0.384); */
    text-align: center;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    /* font-weight: bold; */
}
.sdamage .base div div>span{
    color: var(--color3);
    display: inline-block;
    /* background-color: #fff; */
    text-align: center;
    width: 3rem;
}
.sdamage .base>div{
    height: 2.3rem;
    line-height: 2.3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 0.1rem solid var(--color5);
    
}
.sdamage .base div:last-child{
    border-bottom: none;
}
.sdamage .base>div div img{
    width: 1.5rem;
    height: 1.5rem;
    margin-left: 0rem;
    margin-top: 0rem;
    /* background-color: #fff; */
}
.sdamage .base div:nth-child(1){
    display: flex;
    flex-wrap: nowrap;
    transition: 0.5s;
    border: none;
    margin-top: -0.6rem;
    cursor: pointer;
    width: 86%;
    position: relative;
    left: 50%;
    transform: translate(-50%,0);
    /* background-color: #fff; */
}
.sdamage .base h3{
    width: 100%;
    flex-shrink: 0;
    border-bottom: 0.1rem solid var(--color5);
    transition: 0.5s;
}
.sdamage .base h3:nth-child(2){
    /* background-color: #fff; */
    opacity: 0;
}
.sdamage:hover .base div:nth-child(1){
    transform: translate(-150%,0);
}
.sdamage:hover .base h3:nth-child(1){
    opacity: 0;
}
.sdamage:hover .base h3:nth-child(2){
    opacity: 1;
}