.myselect {
    position: relative;
    height: 2rem;
    display: inline-block;
    margin: 0;
    margin-left: 1rem;
    margin-right: 0.2rem;
}

.selectwrapper {
    overflow: hidden;
    transition: 0.2s ease-out;
    max-height: 16rem;
    min-height: 2rem;
    height: 2rem;
    position: absolute;
}


.myselect .arrow {
    width: 0.7rem;
    height: 0.7rem;
    box-sizing: border-box;
    /* 利用透明边框挤出三角形 */
    border: 0.7rem solid transparent;
    border-top: 0.7rem solid var(--color3);
    position: absolute;
    top: 27%;
    right: 0.7rem;
    border-radius: 0;
}

.myselect .shallow{
    border-top: 0.7rem solid var(--color5);
}

.myselect .input {
    /* width: 8rem; */
    height: 2rem;
    border: 0.1rem solid;
    border-color: var(--color3);
    /* box-shadow: 0 0 0.3rem var(--color3); */
    position: relative;
    border-radius: 0.4rem;
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    justify-content: space-between;
    background-color: white;
    cursor: pointer;
    user-select: none;
}

.input {
    width: 8rem;
}
.input .selectshow img{
    max-height: 76%;
}

.myselect .input>div:nth-child(1) {
    border: none;
    outline: none;
    width: 88%;
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: transparent;
    font-size: 1.1rem;
    color: var(--theme);
    line-height: 1.7rem;
    text-align: center;
}


.myselect .list {
    position: absolute;
    top: 1.9rem;
    width: 100%;
    border: 0.05rem solid var(--color3);
    background-color: var(--color8);
    cursor: default;
    border-radius: 0.2rem;
    z-index: 99999;
}
.myselect .list ul{
    max-height: 13rem;
    overflow: auto;
}
.myselect .list ul li {
    width: 100%;
    height: 2.2rem;
    line-height: 1.7rem;
    margin-bottom: 0.2rem;
    padding: 0.2rem;
    padding-left: 1rem;
    position: relative;
}
.myselect .list ul li img{
    max-height: 80%;
}
.myselect .list ul li::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 2.2rem;
    bottom: -0.2rem;
    /* height: 0.3rem; */
    /* background-color: rgba(214, 44, 44, 0.548); */
    background-color: var(--color8);
}

.myselect .list ul li:last-child {
    margin-bottom: 0rem;
}

.myselect .list ul li:last-child::after {
    content: none;
}

.myselect .list ul li:hover {
    background-color: rgba(0, 0, 0, 0.08);
}


.myselect .selectwrapper input[type=number]::-webkit-inner-spin-button,
.myselect .selectwrapper input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.myselect .selectwrapper input[type=number] {
    -moz-appearance: textfield;
}

.temp {
    position: absolute;
    opacity: 0;
    width: 0;
    height: 0;
    font-size: 0;
}