.dialog{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 9999999999;
    /* width: 100rem;
    height: 100rem; */
    background-color: rgba(0, 0, 0, 0.35);
}
.dialog-inner{
    min-height: 10rem;
    min-width: 24rem;
    background-color: #fff;
    border-radius: 0.3rem;
    position: relative;
    left: 50%;
    top: 40%;
    transform: translate(-50%,-50%);
}
.dialog-title{
    width: 100%;
    height: 3rem;
    line-height: 3rem;
    font-size: 1.3rem;
    /* background-color: antiquewhite; */
    text-align: center;
    color: var(--color3);
}
.dialog .content{
    width: 86%;
    padding: 1rem;
    margin: 0rem auto;
    padding-bottom: 4.4rem;
    border-top: solid 0.1rem var(--color6);
    line-height: 1.4rem;
    letter-spacing: 0.02rem;
    text-align: center;
}
.dialog .btns{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 2.4rem;
    display: flex;
}
.dialog .btns>span{
    display: inline-block;
    width: 35%;
    height: 2.4rem;
    line-height: 2.3rem;
    font-size: 1.4rem;
    border-radius: 0.2rem;
    text-align: center;
    cursor: pointer;
    letter-spacing: 0.18rem;
    margin: 0 auto;
    color: #fff;
    background-color: var(--theme);
    position: relative;
    bottom: 0.4rem;
    transition: 0.2s;
}
.dialog .btns>div{
    width: 50%;
    line-height: 2.2rem;
    font-size: 1.4rem;
    border: solid 0.1rem var(--color7);
    text-align: center;
    cursor: pointer;
    letter-spacing: 0.18rem;
    transition: 0.16s;

}
.dialog .btns>div:nth-child(1){
    color: var(--color3);
}
.dialog .btns>div:nth-child(2){
    color: #fff;
    background-color: var(--theme);
    border: none;
}
.dialog .btns>span:hover{
    /* box-shadow: 0 0 0.4rem var(--color3); */
    width: 45%;
}
.dialog .btns>div:hover{
    width: 80%;
}
