.myinput{
    position: relative;
    height: 2rem;
    display: inline-block;
    margin: 0;
    margin-left: 1rem;
    margin-right: 0.2rem;
}


.inputwrapper{
    overflow: hidden;
    transition: 0.2s ease-out;
    max-height: 20rem;
    min-height: 2rem;
    height: 2rem;
}


.inputwrapper .arrow {
    width: 0.7rem;
    height: 0.7rem;
    box-sizing: border-box;
    /* 利用透明边框挤出三角形 */
    border: 0.7rem solid transparent;
    border-top: 0.7rem solid var(--color3);
    position: absolute;
    top: 27%;
    right: 0.7rem;
    border-radius: 0;
    cursor: pointer;
}
.myinput .shallow{
    border-top: 0.7rem solid var(--color5);
}
.inputwrapper .input {
    height: 2rem;
    border: 0.1rem solid;
    border-color: var(--color5);
    box-shadow: none;
    position: relative;
    border-radius: 0.4rem;
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    justify-content: space-between;
    background-color: var(--color4);
}

.input {
    width: 8rem;
}

.inputwrapper .input input {
    border: none;
    outline: none;
    width: 88%;
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: transparent;
}


.inputwrapper .list {
    position: absolute;
    top: 1.9rem;
    width: 100%;
    max-height: 18rem;
    border: 0.05rem solid var(--color3);
    overflow: auto;
    background-color: var(--color4);
    cursor:default;
    border-radius: 0.2rem;
    z-index: 99999;
}

.inputwrapper .list ul li {
    width: 100%;
    height: 2.3rem;
    cursor: pointer;
    line-height: 1.7rem;
    margin-bottom: 0.3rem;
    position: relative;
    padding: 0.3rem;
    padding-left: 1rem;
}
.inputwrapper .list ul li::after{
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 2.3rem;
    bottom: -0.3rem;
    /* height: 0.3rem; */
    /* background-color: rgba(214, 44, 44, 0.548); */
    background-color: var(--color4);
}

.inputwrapper .list ul li:last-child{
    margin-bottom: 0rem;
}
.inputwrapper .list ul li:last-child::after {
    content: none;
}

.inputwrapper .list ul li:hover {
    background-color: rgba(0, 0, 0, 0.08);
}


.inputwrapper input[type=number]::-webkit-inner-spin-button, 
.inputwrapper input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none;
  margin: 0; 
}
.inputwrapper input[type=number] {
    -moz-appearance:textfield;
}