.schoose{
    font-size: 1rem;
}
.schoose h3{
    margin-top: -0.3rem;
    margin-bottom: 0.5rem;
}
.schoose .base>div img{
    width: 1.4rem;
    height: 1.4rem;
    margin-left: 1.4rem;
}
.schoose .base div div{
    margin-right: 0.4rem;
    width: 4rem;
    /* background-color: rgba(255, 255, 255, 0.384); */
    text-align: left;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
}
.schoose .base div div span{
    color: var(--color3);
    display: inline-block;
    /* background-color: #fff; */
    text-align: center;
    width: 3rem;
}
.schoose .base>div{
    height: 2.3rem;
    line-height: 2.3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 0.1rem solid var(--color5);
}
.schoose .base div:last-child{
    border-bottom: none;
}
.schoose .base>div div img{
    width: 1.8rem;
    height: 1.8rem;
    margin-left: 0rem;
    margin-top: 0rem;
    /* background-color: #fff; */
}
.schoose .base div:nth-child(1){
    display: flex;
    flex-wrap: nowrap;
    transition: 0.5s;
    border: none;
    margin-top: -0.6rem;
    cursor: pointer;
    width: 86%;
    position: relative;
    left: 50%;
    transform: translate(-50%,0);
    /* background-color: #fff; */
}
.schoose .base h3{
    width: 100%;
    flex-shrink: 0;
    border-bottom: 0.1rem solid var(--color5);
    transition: 0.5s;
}
.schoose .base h3:nth-child(2){
    /* background-color: #fff; */
    opacity: 0;
}
.schoose:hover .base div:nth-child(1){
    transform: translate(-150%,0);
}
.schoose:hover .base h3:nth-child(1){
    opacity: 0;
}
.schoose:hover .base h3:nth-child(2){
    opacity: 1;
}