.tips {
    position: relative;
    display: inline-block;
    top: -0.5rem;
    left: 0.24rem;
    height: 0rem;
}

.tips>.txtwrapper {
    border: 0.3rem double var(--color4);
    border-radius: 1rem;
    max-width: 25.3rem;
    font-size: 0.9rem;
    padding: 0.5rem;
    display: block;
    position: absolute;
    left: 50%;
    /* transform: translate(-500%,-100%); */
    /* right: 4rem; */
    /* left: 1.4rem; */
    top: 2.6rem;
    letter-spacing: 0.08rem;
    line-height: 1.4rem;
    transition: .08s 0.05s;
    transform: translate(-50%,0) scale(1, 0);
    /* transition-timing-function: ease-out; */
    transform-origin: top;
    background-color: var(--color3);
    color: var(--color4);
    z-index: 999;
    text-align: center;
    margin: 0;

    /* top: -3rem; */
}

.tips>img {
    width: 0.78rem;
    border-radius: 50%;
    background-color: var(--theme);
    cursor: pointer;

}

.tips.t-2>.txtwrapper {
    font-size: 1rem;
    letter-spacing: 0.11rem;
    line-height: 1.6rem;
    max-width: 29.3rem;
    padding: 0.7rem;
    top: 2.9rem;
}

.tips.t-2>img {
    width: 0.94rem;
}

.tips.t-2 {
    top: -0.7rem;
    left: 0.1rem;
}



.tips.t-3 {
    top: -0.7rem;
    left: 0.1rem;
}

.tips.t-3>.txtwrapper {
    font-size: 1.1rem;
    letter-spacing: 0.14rem;
    line-height: 1.8rem;
    max-width: 33.3rem;
    padding: 0.8rem;
    top: 3rem;
    font-weight: normal;
}

.tips.t-3>img {
    width: 1.22rem;
}




.tips img:hover+.txtwrapper {
    transform: translate(-50%,0) scale(1);
}