.addchar{
    width: 100vw;
    position: relative;
    overflow: auto;
}
.addchar .form{
    position: absolute;
    width: 78vw;
    left: 50%;
    top: 6vw;
    transform: translate(-50%,0);
    /* background-color: antiquewhite; */
    padding: 1rem;
}
.addchar>h3{
    margin-top: 2.4rem;
    font-size: 1.2rem;
    letter-spacing: 0.1rem;
}
.addchar .form>div{
    line-height: 2rem;
    margin-bottom: 2rem;
}
.addchar .form .btn{
    line-height: 3rem;
}
.addchar .form .row div{
    word-spacing: 0.7rem;
    margin-bottom: 1rem;
    vertical-align: top;
}
.addchar .form h3{
    margin-bottom: 1.2rem;
}
.addchar .foot{
    display: flex;
    flex-direction: row-reverse;
}
.addchar .addskill{
    display: block;
    width: 100%;
    background-color: #fff;
}
.shige input[type="text"]{
    font-size: 1rem;
}

.pic .selectshow img{
    height: 70%;
    position: relative;
    top: 12%;
    margin-right: 0.2rem;
}
.pic ul li{
    vertical-align: top;
}
.pic ul li img{
    position: relative;
    top: 15%;
    margin-right: 0.4rem;
}