.topbar a {
    position: relative;
    display: inline-block;
    padding: 1em 1em;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    color: white;
    font-size: 1.4rem;
}

.topbar a::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: linear-gradient(135deg, var(--theme), var(--btn));
    border-radius: 0.4rem;
    transition: box-shadow 0.3s ease;
    will-change: transform;
    box-shadow: 0 0.1rem 0.3rem rgba(0, 0, 0, 0.2);
}

.topbar a:hover::before {
    box-shadow: 0 0.3rem 1rem rgba(0, 0, 0, 0.3);
}

.topbar a::after {
    position: relative;
    display: inline-block;
    content: attr(data-title);
    /* font-weight: bold; */
    letter-spacing: 0.01rem;
    letter-spacing: 0.4rem;
}

.topbar {
    margin: 1rem;
    display: flex;
    justify-content: space-between;
}

.topbar>div {
    background: linear-gradient(135deg, var(--theme), var(--btn));
    border-radius: 0.4rem;
    cursor: pointer;
    width: 4.3rem;
    transition: 0.3s;
    box-shadow: 0 0.1rem 0.3rem rgba(0, 0, 0, 0.2);

}

.topbar>div:hover {
    box-shadow: 0 0.2rem 0.8rem rgba(0, 0, 0, 0.3);
}

.topbar>div>img {
    width: 100%;
}

.topbar>div:nth-child(3)>img {
    width: 84%;
    margin-left: 8%;
    margin-top: 8%;
}


.zhezhao .bar {
    /* display: block; */
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.45);
    z-index: 999;
    display: flex;
    justify-content: space-around;
}

.zhezhao .bar>div:nth-child(1) {
    width: 4rem;
    height: 4rem;
    position: absolute;
    border-radius: 50%;
    background-color: #fff;
    border: 0.6rem solid #fff;
    box-shadow: 0 0 0.6rem rgb(148, 206, 233);
    box-sizing: content-box;
    cursor: pointer;
    right: 5rem;
    top: 3rem;
}

.zhezhao .bar>div:nth-child(1) img {
    width: 100%;
}

.zhezhao .bar>div:nth-child(1):hover {
    transform: rotate(180deg);
    transition: 0.4s;
}

.zhezhao .bar .jieshao {
    position: absolute;
    font-size: 1.6rem;
    color: #fff;
    top: 18rem;
    letter-spacing: 0.1rem;
}

.zhezhao .bar .btn {
    width: 35%;
    margin-top: 33rem;
    height: 6rem;
    font-size: 2rem;
    line-height: 5.7rem;
    color: #333;
    background-color: azure;
    letter-spacing: 0.5rem;
    border: 0.2rem solid rgb(148, 206, 233);
    box-shadow: 0 0 0.7rem rgb(148, 206, 233);
    transition: 0.4s;
    border-radius: 2.8rem;
}

.zhezhao .bar a{
    display: none;
}
.zhezhao .bar input {
    position: absolute;
    top: 25rem;
    width: 16.6rem;
    height: 3.6rem;
    line-height: 3rem;
    text-align: center;
    /* background-color: transparent; */
    border: none;
    border-radius: 0.3rem;
    padding: 0.3rem 1rem;
}

@keyframes run {
    0% {
        box-shadow: 0 0 0.7rem rgb(148, 206, 233);
    }
    25% {
        box-shadow: 0 0 1.1rem rgb(133, 216, 255);
    }
    50% {
        box-shadow: 0 0 0.7rem rgb(148, 206, 233);
    }
    75% {
        box-shadow: 0 0 0.2rem rgb(148, 206, 233);
    }
    100% {
        box-shadow: 0 0 0.7rem rgb(148, 206, 233);
    }
}

.zhezhao .bar .btn:hover {
    animation: run infinite 2.4s linear;
    border: 0.2rem solid rgb(83, 128, 170);
}